export default {
	name: 'Popup',
	data(){
		return {
			onTouch: false
		}
	},
	methods: {
		startTouch() {
			this.onTouch = true
		},
		endTouch() {
			this.onTouch = false
		},
		goSelect() {
			uni.redirectTo({
				url: '/pages/books/books'
			})
		}
	}
}
